.btn {
  display: inline-block;
  text-align: center;
  padding: 4px 8px 4px 8px;
  margin: 0 2px 0 2px;
  background: #444;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.btn:hover {
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
  -webkit-transform: translate(0, -2px);
      -ms-transform: translate(0, -2px);
          transform: translate(0, -2px);
  -webkit-box-shadow: 0px 4px 3px -2px gray;
          box-shadow: 0px 4px 3px -2px gray;
}
.disabled {
  pointer-events: none;
  opacity: 0.5;
  background: #CCC;
}

.btn-info {
  background: #0077ff;
  color: white;
}

.btn-danger {
  /* background: #ee2222; */
  background: #FF3C3C;
  color: white;
}

.btn-warning {
  background: #ddaa00;
  color: white;
}

.btn-success {
  background: #44aa33;
  color: white;
}


