.App {
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
}

.App-header {
  background-color: #222;
  height: 48px;
  padding: 8px 4px 8px 4px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
      align-items: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: inherit;
}

.App-name {
  display: inline-block;
  font-size: 20px;
  -ms-flex: 4 1;
      flex: 4 1;
}

.App-nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
      align-items: center;
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 36px;
}

.App-nav-item{
  padding: 8px;
  background-color: #333;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  text-align: center;
}
.App-nav-item a{
  display: inline-block;
  width: 100%;
  color: white !important;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 400;
}
.App-nav-item.active{
  -webkit-filter: brightness(140%);
          filter: brightness(140%);
}
.App-nav-item:hover{
  -webkit-filter: brightness(140%);
          filter: brightness(140%);
}

.App-content {
  position: relative;
  padding: 4px;
  margin-top: 44px;
  bottom: 44px;
}
.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



