input {
  display: inline-block;
  padding: 4px 8px 4px 8px;
  margin: 0 2px 0 2px;
  font-family: 'Raleway', sans-serif;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  border: none;
  border-bottom: 1px solid gray;
}

.flex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}



